.product-list {
  margin-left: 100px;
  margin-right: 100px;
  padding: 0;
}

.product-list_item {
  float: left;
  max-width: 400px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 15px;
  list-style: none;
  padding: 25px;
  -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
          box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.cart-button {
  cursor: pointer;
  border: 0;
  padding: 2px;
  background-color: #febd69;
  width: 75px;
}
.cart-button:hover {
  background-color: #f5d78e;
}



